* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0px;
    font-family: 'Balsamiq Sans', cursive;
  }

  .topHeader-main{
    position: relative;
    z-index: 99999;
    background: #fff;
  }
  
 .top_nav{
  position: relative;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
 }
 
 .my_name{
   display: inline;
   float: left;
   font-family: 'Dancing Script', cursive;
   font-size: 24px;
   font-weight: 700;
 }
 .my_contact{
   display: inline;
   float: right;
 }

  .nav {
    height: 50px;
    width: 100%;
    background-color: #7f58af;
    position: relative;
    display: inline;
    float: left;
  }
  
  .nav > .nav-header {
    display: inline;
    height: 50px;
  }
  
  .nav > .nav-header > .nav-title {
    display: inline-block;
    font-size: 22px;
    color: #fff;
    padding: 0px 10px;
    height: 50px;
    line-height: 50px;
  }
  .myNameLink{
    color:#fff;
  }
  .myNameLink:hover{
    color:#fff;
    text-decoration: none;
  }
  .nav > .nav-btn {
    display: none;
  }
  
  .nav > .nav-links {
    display: inline;
    float: right;
    height: 50px;
  }
  
  .nav > .nav-links > a {
    display: inline-block;
    padding: 0px 15px;
    text-decoration: none;
    color: #efefef;
    height: 50px;
    line-height: 50px;
  }
  
  .nav > .nav-links > a:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  
  .nav > #nav-check {
    display: none;
  }
  
  .active_menu{
    background: #542390;
    color: #fff;
  }


  @media (max-width:600px) {
    .nav > .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;
    }
    .nav > .nav-btn > label {
      display: inline-block;
      width: 50px;
      height: 50px;
      padding: 13px;
    }
    .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
      background-color: rgba(0, 0, 0, 0.3);
    }
    .nav > .nav-btn > label > span {
      display: block;
      width: 25px;
      height: 10px;
      border-top: 2px solid #eee;
    }
    .nav > .nav-links {
      position: absolute;
      display: block;
      width: 100%;
      background-color: #333;
      height: 0px;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 50px;
      left: 0px;
    }
    .nav > .nav-links > a {
      display: block;
      width: 100%;
    }
    .nav > #nav-check:not(:checked) ~ .nav-links {
      height: 0px;
    }
    .nav > #nav-check:checked ~ .nav-links {
      height: calc(100vh - 50px);
      overflow-y: auto;
    }
  }